import { Component, Element, Event, EventEmitter, Fragment, h, Host, JSX, Listen, Prop, State } from '@stencil/core';
import { getPPNDealersByRegion } from '../../services/dealer-service';
import { splitLocale } from '../../entities/locale';
import { City, DealersByRegion } from '../../entities/dealer-response';
import { dealerSearchResultsStore } from '../../state/dealer-search-results-store';
import { getErrorMessage } from '../../utility/navi-error';
import Logger from '../../services/logger';
import { navContentStore } from '../../state/nav-content-store';
import ErrorMessage from '../dealer-search-form/error-message';
import { isString } from '../../utility/helper';
import { componentsOnReady } from '../../utility/ready';
import { Timeout } from '../../utility/timeout';
import {
  DEALER_SEARCH_RESULT_DISPLAY_ELEMENT_ID_DOUBLE_DRAWER,
  ElementTypes,
  EventActions,
  GEOLOCATION_BUTTON_SEARCH_TERM
} from '../../utility/constants';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import analyticsManager from '../../services/analytics/analytics-manager';

@Component({
  tag: 'phn-dealer-search-form-dropdown',
  styleUrl: 'dealer-search-form-dropdown.scss',
  scoped: true
})
export class DealerSearchFormDropdown {
  @Prop() locale = '';
  @Prop() breakpoint!: number;

  @State() isLoading = false;
  @State() errorMessage: string | null = null;
  @State() dealersOfRegion: DealersByRegion = { regions: [] };
  @State() cities: City[] = [];
  @State() selectedProvince = '';
  @State() selectedCity = '';
  @State() showSkeleton = true;

  @Event() dealerSearchFormEvent!: EventEmitter;
  @Event() clearSearchEvent!: EventEmitter;
  @Event() removeSkeletonOnDealerSearchForm!: EventEmitter;

  @Element() el!: HTMLPhnDealerSearchFormDropdownElement;

  componentWillLoad(): void {
    this.waitForComponentsToLoad();
    this.getDealersByRegion();
  }

  componentDidLoad(): void {
    this.emitPageLoadEvent();
  }

  private async waitForComponentsToLoad() {
    await componentsOnReady(this.el, new Timeout(5000));
    this.removeSkeletonOnDealerSearchForm.emit();
  }

  @Listen('removeSkeletonOnDealerSearchForm')
  loadedAutoCompleteListener(_event: Event): void {
    this.showSkeleton = false;
  }

  private async getDealersByRegion() {
    const { error: errorContent } = navContentStore.state;
    const { country } = splitLocale(this.locale);

    try {
      this.isLoading = true;
      this.dealersOfRegion = await getPPNDealersByRegion(country);
    } catch (err) {
      Logger.error('FETCH_DEALERS', err as Error);
      this.errorMessage = getErrorMessage(errorContent, err as Error);
    } finally {
      this.isLoading = false;
      this.dealerSearchFormEvent.emit();
      this.emitResultsLoadEvent();
    }
  }

  private onCitySelect(event: Event): void {
    /* istanbul ignore if */
    if (event.target === null) {
      return;
    }

    const selectedCity = (event.target as HTMLSelectElement).value;
    this.selectedCity = selectedCity;
    dealerSearchResultsStore.state.ppnDealers =
      this.cities.find((city) => {
        return [city.cityName, city.cityNameLocalized].includes(selectedCity);
      })?.dealers || {};
    dealerSearchResultsStore.state.results = Object.keys(dealerSearchResultsStore.state.ppnDealers);
    dealerSearchResultsStore.state.searchTerm = `${this.selectedProvince}, ${this.selectedCity}`;
  }

  private onProvinceSelect(event: Event): void {
    /* istanbul ignore if */
    if (event.target === null) {
      return;
    }

    const selectedProvince = (event.target as HTMLSelectElement).value;
    this.cities = this.getCitiesByGivenRegionName(selectedProvince);
    this.selectedProvince = selectedProvince;
  }

  private getCitiesByGivenRegionName(province: string): City[] {
    const regions = this.dealersOfRegion.regions;

    const selectedRegion = regions.find(({ regionName, regionNameLocalized }) =>
      [regionName, regionNameLocalized].includes(province)
    );

    return selectedRegion?.cities || [];
  }

  private emitPageLoadEvent() {
    const { results, searchTerm } = dealerSearchResultsStore.state;
    const { dealer } = navContentStore.state;

    globalDataLayerService.pushDataLayerEvent(EventActions.DEALER_SEARCH_SCREEN_LOAD, {
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType('', ElementTypes.dealerSearchForm),
        displayElementName: dealer.text
      },
      componentSearch: {
        searchTerm: isString(searchTerm) ? searchTerm : GEOLOCATION_BUTTON_SEARCH_TERM,
        numberOfResults: results?.length || 0
      }
    });
  }

  private emitResultsLoadEvent() {
    const { results, searchTerm } = dealerSearchResultsStore.state;
    const { dealer: dealerContent } = navContentStore.state;

    const numberOfResults = results?.length || 0;

    globalDataLayerService.pushDataLayerEvent(EventActions.DEALER_RESULTS_SCREEN_LOAD, {
      componentDisplay: {
        displayElementId: analyticsManager.getDealerResultsLoadDisplayElementId(
          DEALER_SEARCH_RESULT_DISPLAY_ELEMENT_ID_DOUBLE_DRAWER
        ),
        displayElementType: analyticsManager.getDealerResultsLoadDisplayElementType(),
        displayElementName: dealerContent.text
      },
      componentSearch: {
        searchTerm: searchTerm,
        numberOfResults: numberOfResults
      }
    });
  }

  private reset() {
    this.errorMessage = null;
    this.isLoading = false;
    this.selectedProvince = '';
    this.selectedCity = '';

    dealerSearchResultsStore.state.results = null;
    dealerSearchResultsStore.state.savedDealerCookie = null;

    this.clearSearchEvent.emit();
  }

  render(): JSX.Element {
    const { results } = dealerSearchResultsStore.state;
    const { dealer: dealerContent } = navContentStore.state;
    const searchText = dealerSearchResultsStore.state.searchTerm;

    if (this.isLoading) {
      return (
        <div class="spinner">
          <phn-p-spinner size="medium" aria={{ 'aria-label': 'Loading page content' }} />
        </div>
      );
    }

    return (
      <Host>
        {results && results.length > 0 ? (
          <form
            class="form-container"
            onSubmit={(event) => {
              event.preventDefault();
            }}
          >
            <div>
              <div class="phn-flex-row phn-align-center phn-justify-between title-container">
                <div class="text-limit">
                  <phn-p-text
                    weight="regular"
                    size="small"
                    tag="div"
                    ellipsis="true"
                    color="contrast-medium"
                    data-test-id="dealer-search-form-input"
                  >
                    {searchText}
                  </phn-p-text>
                </div>
                <phn-p-icon
                  name="close"
                  class="close-icon"
                  style={{ display: this.showSkeleton ? 'none' : '', cursor: 'pointer' }}
                  onClick={(event: Event) => {
                    event.preventDefault();
                    this.reset();
                  }}
                />
              </div>
            </div>
          </form>
        ) : (
          <Fragment>
            <phn-p-select-wrapper
              filter
              label={dealerContent.search.dropdown.province}
              required
              style={{ display: this.showSkeleton ? 'none' : '' }}
            >
              <select
                name="dealers-province"
                onChange={(event) => this.onProvinceSelect(event)}
                style={{ display: this.showSkeleton ? 'none' : '' }}
              >
                <option hidden selected>
                  {dealerContent.search.dropdown.selectAProvince}
                </option>
                {this.dealersOfRegion.regions.map((region) => {
                  const province =
                    splitLocale(this.locale).language === 'en' || region.regionNameLocalized === null
                      ? region.regionName
                      : region.regionNameLocalized;
                  return <option value={province}>{province}</option>;
                })}
              </select>
            </phn-p-select-wrapper>

            <phn-p-select-wrapper
              style={{ display: this.showSkeleton ? 'none' : '' }}
              class="city-select-form-field"
              filter="true"
              label={dealerContent.search.dropdown.city}
              required
            >
              <select
                disabled={!isString(this.selectedProvince)}
                style={{ display: this.showSkeleton ? 'none' : '' }}
                name="dealers-cities"
                onChange={(event) => {
                  this.onCitySelect(event);
                }}
              >
                <option hidden>{dealerContent.search.dropdown.selectACity}</option>
                {this.cities.map((city) => (
                  <option>
                    {splitLocale(this.locale).language === 'en' || city.cityNameLocalized === null
                      ? city.cityName
                      : city.cityNameLocalized}
                  </option>
                ))}
              </select>
            </phn-p-select-wrapper>

            {isString(this.errorMessage) ? (
              <ErrorMessage errorMessage={this.errorMessage} onDismiss={() => this.reset()} />
            ) : null}
          </Fragment>
        )}
      </Host>
    );
  }
}
