@import '../../scss';

:host {
  display: block;

  .spinner {
    display: flex;
    position: relative;
    justify-content: center;
    width: 100%;
    margin-top: p-px-to-rem(24px);
  }

  .city-select-form-field{
    padding-top: p-px-to-rem(24px);
  }

  .title-container {
    display: flex;
    flex-direction: row;
    border: 2px solid rgb(107, 109, 112);
    border-radius: 5px;

    transition: background-color 0.24s ease;
    margin: 0;
    min-height: p-px-to-rem(54px);

    phn-p-text {
      padding-left: 19px;
    }


    phn-p-icon{
      margin-right: p-px-to-rem(16px);
      padding-top: -8px;
      outline: 0;
    }

    div.text-limit {
      width: 80%;
    }
  }
}
