@import '../../scss/index';

:host {
  display: block;

  &.skeleton {
    pointer-events: none;
    user-select: none;

    & .skeleton-autocomplete-search-field {
      width: auto;
      height: p-px-to-rem(50px);
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
    }

    & .skeleton-or-text {
      width: auto;
      height: p-px-to-rem(20px);
      background: $pds-theme-light-contrast-low;
      border-radius: 4px 4px 0 0;
    }

    & .skeleton-geolocation-button {
      width: auto;
      height: p-px-to-rem(50px);
      background: $pds-theme-light-contrast-low;
      border-radius: 0 0 4px 4px;
    }
  }

  .or-container {
    position: relative;
    width: 100%;
    margin-top: p-px-to-rem(20px);

    .geolocation {
      width: 100%;
      padding-bottom: 24px;
    }

    phn-p-text {
      margin-bottom: p-px-to-rem(4px);
      color: $pds-theme-light-primary;
    }
  }

  .animate-out {
    animation: fadeOutToTop 0.75s ease-in-out 0s;
  }

  .input-container {
    width: 100%;
  }

  .spinner {
    display: flex;
    position: relative;
    justify-content: center;
    width: 100%;
    margin-top: p-px-to-rem(24px);
  }
}
