@import '../../scss';

:host {
  display: block;

  .form-container {
    display: block;
    position: relative;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    box-sizing: border-box;

    phn-p-text-field-wrapper {
      width: 100%;

      input[type=search]::-ms-clear {  display: none; width : 0; height: 0; }
      input[type=search]::-ms-reveal {  display: none; width : 0; height: 0; }
      input[type="search"]::-webkit-search-decoration,
      input[type="search"]::-webkit-search-cancel-button,
      input[type="search"]::-webkit-search-results-button,
      input[type="search"]::-webkit-search-results-decoration { display: none; }
    }

    &.inline-search-form {
      margin-right: p-px-to-rem(16px);
    }
  }

  .prediction-container {
    box-sizing: border-box;
    position: absolute;
    z-index: 10;
    width: 100%;

    background-color: white;
    border: 1px solid $pds-theme-light-contrast-low;

    ul {
      margin-block: 0;
      margin-inline: 0;
      padding-inline: 0;
      list-style-type: none;

      li {
        padding-left: p-px-to-rem(12px);
        padding: p-px-to-rem(8px) 0 p-px-to-rem(8px) p-px-to-rem(12px);
        color: $pds-theme-light-primary;

        &:hover {
          color: $pds-theme-light-notification-info;
          background-color: $pds-theme-light-background-surface;
        }

        &.highlighted-prediction {
          color: $pds-theme-light-notification-info;
        }
      }
    }

    .powered-by-google {
      margin: p-px-to-rem(12px) 0 p-px-to-rem(12px) p-px-to-rem(12px);
    }
  }
}
