@import '../../scss';

:host {
  display: block;

  margin-top: p-px-to-rem(26px);

  @include pds-media-query-min('s') {
    margin-top: p-px-to-rem(-2px);
  }

  @include pds-media-query-min('l') {
    margin-top: p-px-to-rem(-3px);
  }

  @include pds-media-query-min('xxl') {
    margin-top: p-px-to-rem(-7px);
  }

  .skeleton-headline {
    width: auto;
    height: p-px-to-rem(50px);
    background: $pds-theme-light-contrast-low;
    margin-bottom:  p-px-to-rem(28px);
    border-radius: 4px;
  }

  phn-p-heading{
    margin-bottom: p-px-to-rem(24px);
  }

  phn-menu-item {
    margin-top: p-px-to-rem(24px);

    &:first-of-type {
      margin-top: p-px-to-rem(14px);
    }
  }

  &.skeleton {
    pointer-events: none;
    user-select: none;

    &.skeleton-menu-item {
      width: auto;
      height: p-px-to-rem(20px);
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
    }
  }
}
